html, body, #root {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
  width: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.chat-wrapper {
  display: flex;
  height: 100%;
  width: 100%;
  flex-direction: column;
}

.chat-wrapper .chat-body {
  flex-grow: 1;
  overflow-x: scroll;
  padding-bottom: 5px;
}

.chat-wrapper .chat-input {
  background-color: cadetblue;
}

.chat-wrapper .chat-input .chat-box {
  display: flex;
  width: 100%;
  padding: 10px;
  height: 30px;
  font-size: 14px;
  outline: none;
  border-top: 1px solid #000;
}

.chat-wrapper .msg-bubble {
  padding: 3px 10px;
  font-size: 14px;
}

.chat-wrapper .bubble-owner {
  margin: 5px 10px;
  background-color: rgb(220, 241, 248);
  border-radius: 30px;
  margin-left: 30%;
  text-align: right;
  padding: 10px;
}

.chat-wrapper .bubble-recipient {
  margin: 5px 10px;
  background-color: rgb(247, 241, 231);
  border-radius: 30px;
  margin-right: 30%;
  text-align: left;
  padding: 10px;
}

.render-p2p {
  padding: 20px;
}
